* {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
}
body {
  background-color: rgb(105, 52, 175);
}
main {
  padding: 15px;
}
.container {
  padding: 5px;
  max-width: 1300px;
  text-align: center;
  background-color: white;
  margin: auto;
  margin-top: 50px;
  border-radius: 5px;
}
.header {
  width: 100%;
  text-align: center;
  margin-top: 30px;
}
.theLine {
  width: 100px;
  height: 8px;
  border-radius: 2px;
  margin: auto;
  background-color: rgb(105, 52, 175);
  margin-top: 5px;
}
.catagoriesontainer {
  width: 100%;
}
.buttonsContainer {
  padding: 5px;
  width: 100%;
  margin: auto;
  margin-top: 20px;
  /* min-width: 250px; */
}
.btn {
  font-size: 16px;
  border-color: transparent;
  background-color: inherit;
  color: rgb(105, 52, 175);
  margin: 10px;
  padding: 3px 10px;
  border-radius: 2px;
  text-align: center;
  outline-color: rgb(105, 52, 175);
  transition: 0.4s ease-in-out;
  text-transform: capitalize;
}

.btn:hover {
  background-color: rgb(105, 52, 175);
  color: white;
}

/* ////////// */

article {
  width: 100%;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
}
.menuitem {
  width: 48%;
  display: flex;
  justify-content: space-between;
  text-align: left;
  padding: 5px;
  box-shadow: rgb(105, 52, 175) 2px 2px 5px;
  /* margin: 8px 5px; */
  margin: 8px;
  border-radius: 5px;
}
.Mailheader {
  display: flex;
  justify-content: space-between;
  padding: 5px;
  border-bottom: 2px dotted black;
  text-transform: capitalize;
}
.Mailheader p {
  color: rgb(125, 103, 224);
  font-weight: bold;
  text-decoration: underline;
}
.displayinline {
  display: inline-block;
}
.imgContainer {
  padding: 5px;
  width: 40%;
  height: 150px;
  overflow: hidden;
  border-radius: 5px;
  text-align: center;
  background-color: rgb(173, 121, 121);
}

.imgContainer img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  object-fit: cover;
}
.dataContainer {
  padding: 10px;
  width: 70%;
}

/* responsive  */

@media only screen and (max-width: 900px) {
  article {
    width: 100%;
    display: block;
  }
  .menuitem {
    width: 100%;
    margin: 0;
    margin-top: 10px;
  }
  .imgContainer {
    padding: 5px;
    width: 30%;
    border-radius: 5px;
    text-align: center;
  }
}

@media only screen and (max-width: 500px) {
  .container {
    margin-top: 2px;
  }
  article {
    width: 100%;
    display: block;
  }
  .menuitem {
    width: 100%;
    display: block;
    margin: 0;
    margin-top: 10px;
  }
  .imgContainer {
    padding: 5px;
    width: 100%;
    height: 200px;
    border-radius: 5px;
    text-align: center;
  }
  .dataContainer {
    padding: 10px;
    width: 100%;
  }
  .displayinline {
    display: block;
  }

  .buttonsContainer {
    margin: 0;
    justify-content: left;
  }
  .btn {
    font-size: 16px;
    margin: 0;
    padding: 3px 10px;
    text-align: center;
  }
  main {
    padding: 4px;
  }
}

@media only screen and (max-width: 290px) {
  .Mailheader {
    display: block;
  }
}
